import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import InstituteStaff from "../../../UserStaffMember/InstituteStaff/InstituteStaff";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import CustomAutomateSelect from "../../../Ui/Select/CustomAutomateSelect";
import StringLength from "../../../../Custom/Validation/StringLength";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import FormInput from "../../../Ui/Input/FormInput";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import {
  // useAutomateInstitutePlateformList,
  useAutomateInstituteCreateClass,
} from "../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import {
  useOneDepartmentAddClass,
  useOneDepartmentAllClassMaster,
} from "../../../../Department/Institute/Components/DepartmentApi/depart-api";
const CreateClass = ({
  instituteId,
  departmentId,
  batchId,
  onClose,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    mcId: "",
    classTitle: "",
    classHeadTitle: "",
    sid: "",
    aggregatePassingPercentage: "",
    optionalSubjectCount: "",
    stream: "",
    batchCount: "",
  });
  const [staff, setStaff] = useState("");
  const [step, setStep] = useState(1);
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  // const [streamList, setStreamList] = useState([]);
  // const { automateInstitutePlateformList } =
  //   useAutomateInstitutePlateformList();

  const [addClass] = useOneDepartmentAddClass();
  const [automateInstituteCreateClass] = useAutomateInstituteCreateClass();

  const { allClassMaster, allClassMasterRefetch } =
    useOneDepartmentAllClassMaster({
      data: {
        id: instituteId,
        did: departmentId,
      },
      skip: instituteId ? !instituteId : !departmentId,
    });

  useEffect(() => {
    if (instituteId && departmentId) allClassMasterRefetch();
  }, [instituteId, departmentId]);

  const onIconClose = () => {
    onClose();
  };

  const onSelectStandard = (data) => {
    setState((prev) => ({
      ...prev,
      mcId: data?._id,
    }));
  };
  const onSelectOptionalSubject = (data) => {
    setState((prev) => ({
      ...prev,
      optionalSubjectCount: +data?.name,
    }));
  };
  const onSelectStream = (data) => {
    setState((prev) => ({
      ...prev,
      stream: data?._id,
    }));
  };

  const onInputChange = (e, lnt) => {
    if (e.target.value?.length <= lnt) {
      setState((prev) => ({
        ...prev,
        [e.target.name]: e.target.value,
      }));
    }
  };

  const onInputPassingChange = (e, lnt) => {
    if (+e.target.value <= lnt) {
      setState((prev) => ({
        ...prev,
        aggregatePassingPercentage: +e.target.value,
      }));
    }
  };

  const onInputBatchChange = (data) => {
    setState((prev) => ({
      ...prev,
      batchCount: +data?.name,
    }));
  };
  const onSelectStaff = (val) => {
    setStaff(val);
    setState((prev) => ({
      ...prev,
      sid: val?._id,
    }));
    setStep(1);
  };

  const onCreate = () => {
    let validation = requiredField(state, [
      "sid",
      "aggregatePassingPercentage",
      "optionalSubjectCount",
      "stream",
      "batchCount",
    ]);
    if (
      existingRequiredField(validation) &&
      instituteId &&
      departmentId &&
      batchId
    ) {
      setDisabled((pre) => !pre);
      if (state.batchCount) {
        automateInstituteCreateClass({
          did: departmentId,
          bodyParameter: {
            ...state,
            aggregatePassingPercentage: state.aggregatePassingPercentage
              ? state.aggregatePassingPercentage
              : 0,
            optionalSubjectCount: state.optionalSubjectCount
              ? state.optionalSubjectCount
              : 0,
            batchCount: state.batchCount ? state.batchCount : 0,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      } else {
        addClass({
          classCreate: {
            ...state,
            aggregatePassingPercentage: state.aggregatePassingPercentage
              ? state.aggregatePassingPercentage
              : 0,
            optionalSubjectCount: state.optionalSubjectCount
              ? state.optionalSubjectCount
              : 0,
            batchCount: state.batchCount ? state.batchCount : 0,
          },
          id: instituteId,
          did: departmentId,
          bid: batchId,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      {step === 3 ? (
        <InstituteStaff
          onSelectStaff={onSelectStaff}
          onClose={() => setStep(1)}
          instituteId={instituteId}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_class_n_place")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onIconClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <CustomAutomateSelect
                selectLabel={t("select_standard_grade_place")}
                selectedValue={t("please_select_standard")}
                onClick={onSelectStandard}
                options={allClassMaster?.classMaster ?? []}
                errorShow={errorField["mcId"]}
              />
              <FormInput
                label={t("class_name")}
                placeholder={t("class_name_placeholder")}
                name={"classTitle"}
                value={state.classTitle}
                type={"text"}
                onChange={(e) => onInputChange(e, 45)}
                errorShow={errorField["classTitle"]}
              />
              <StringLength maxLength={45} valueLength={state.classTitle} />

              <div
                className={style.card_container_image}
                onClick={() => setStep(3)}
                style={{
                  cursor: "pointer",
                }}
              >
                <img
                  alt="not found"
                  src={
                    staff?.staffProfilePhoto
                      ? `${imageShowUrl}/${staff.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                />
                <div className={style.card_container_inner}>
                  {!staff?.staffFirstName ? (
                    <>
                      <h6>
                        {t("assign_chead")} {t("optional")}
                      </h6>
                      <p>{t("search_from_joined_list")}</p>
                    </>
                  ) : (
                    <>
                      <h6>{`${staff?.staffFirstName ?? ""} 
                    ${staff?.staffMiddleName ?? ""}
                    ${staff?.staffLastName ?? ""}`}</h6>
                      <p>
                        {t("index_number_1")} {staff?.staffROLLNO ?? ""}
                      </p>
                    </>
                  )}
                </div>
              </div>

              <FormInput
                label={t("give_chead_title")}
                placeholder={t("class_head_example")}
                name={"classHeadTitle"}
                value={state.classHeadTitle}
                type={"text"}
                onChange={(e) => onInputChange(e, 40)}
                errorShow={errorField["classHeadTitle"]}
              />
              <StringLength maxLength={40} valueLength={state.classHeadTitle} />

              <FormInput
                label={`${t("pass_percentage")} ${t("optional")}`}
                placeholder={t("pass_percentage_placeholder")}
                name={"aggregatePassingPercentage"}
                value={state.aggregatePassingPercentage}
                type={"text"}
                onChange={(e) => onInputPassingChange(e, 100)}
              />
              <StringLength maxLength={10} valueLength={state.gr_initials} />

              <CustomAutomateSelect
                selectLabel={t("optional_subject")}
                selectedValue={t("select_number_of_subject")}
                onClick={onSelectOptionalSubject}
                options={[
                  {
                    name: 0,
                  },
                  {
                    name: 1,
                  },
                  {
                    name: 2,
                  },
                  {
                    name: 3,
                  },
                  {
                    name: 4,
                  },
                  {
                    name: 5,
                  },
                ]}
                defalutValue={state.optionalSubjectCount}
              />
              <CustomAutomateSelect
                selectLabel={t("select_batch_count")}
                selectedValue={t("please_select_batch_count")}
                onClick={onInputBatchChange}
                options={[
                  {
                    name: 0,
                  },
                  {
                    name: 1,
                  },
                  {
                    name: 2,
                  },
                  {
                    name: 3,
                  },
                  {
                    name: 4,
                  },
                  {
                    name: 5,
                  },
                ]}
                defalutValue={state.batchCount}
              />
              {/* <CustomAutomateSelect
                selectLabel={t("stream")}
                selectedValue={t("please_select_stream")}
                onClick={onSelectStream}
                options={streamList ?? []}
                errorShow={errorField["stream"]}
              /> */}
              <CreateButton label="create" onAction={onCreate} />
            </UserMemberContentWrapper>

            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default CreateClass;

const nextValidation = (state) => {
  let dt = !state.affiliation_with_institute
    ? requiredField(state, [
        "dName",
        "dTitle",
        "sid",
        "gr_initials",
        "institute_type",
        "institute_type_id",
        "affiliation_with_institute_id",
        "department_type",
        "department_type_id",
        "stream_type",
        "stream_type_id",
      ])
    : state.department_type === "First Year"
    ? requiredField(state, [
        "dName",
        "dTitle",
        "sid",
        "gr_initials",
        "institute_type_id",
        "affiliation_with_institute_id",
        "department_type_id",
        "stream_type",
        "stream_type_id",
      ])
    : state.affiliation_with_institute === "Anonymous"
    ? requiredField(state, [
        "dName",
        "dTitle",
        "sid",
        "gr_initials",
        "institute_type",
        "institute_type_id",
        "affiliation_with_institute_id",
        "department_type",
        "department_type_id",
        "stream_type",
        "stream_type_id",
      ])
    : requiredField(state, [
        "dName",
        "dTitle",
        "sid",
        "gr_initials",
        "institute_type_id",
        "affiliation_with_institute_id",
        "department_type_id",
        "stream_type_id",
      ]);

  return dt;
};
